

a {
  color: white;
}
  
.long {
  width: 100%;
  background-color: #010e19;
}
 
.container {
  height: 400px;
}
  
.scroll {
  opacity: 0.3;
  background-color: aqua;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  border-radius: 5px;
  border: none;
  
 
}

.arrow_up {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -9px;
  margin-left: -5px;
}
  